@import url('https://fonts.googleapis.com/css?family=Lato|PT+Mono');

.table-outer {
  height: calc(100% - 35px);
}
.table-tools {
  height: 35px;
  background-image: -webkit-gradient(linear, left top, right top, from(red), color-stop(green), to(blue));
  background-image: -webkit-linear-gradient(left, red, green, blue);
  background-image: -o-linear-gradient(left, red, green, blue);
  background-image: linear-gradient(90deg, red, green, blue);
}

.table-inner {
  font-family: 'Lato';
  font-size: 12px;
  /* for scroll bars */
  padding: 17px;
}

.columns {
  position: relative;
  display: -ms-flexbox;
  display: flex;
}
.columns:before {
  content: ' ';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 5px;
  background-color: rgba(0, 0, 0, 0.2);
}
.columns:after {
  content: ' ';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 3px;
  background-color: rgba(0, 0, 0, 0.2);
}
.columns-resizer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 8px;
  cursor: col-resize;
  z-index: 9999;
}
.columns-scroll-fix {
  position: absolute;
  width: 100%;
  height: 1px;
  top: 0;
  left: 0;
  pointer-events: none;
}

.column {
  position: relative;
  -ms-flex: auto;
      flex: auto;
}
.column-resizer {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 8px;
  cursor: col-resize;
  z-index: 9999;
}
.column-resizer::before {
  content: ' ';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 3px;
  right: 3px;
  background-color: rgba(0, 0, 0, 0.2);
}

.cell {
  width: 100%;
  height: 25px;
  line-height: 25px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  cursor: default;
  border-bottom: 2px solid rgba(0, 0, 0, 0.2);
}
.cell:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.1);
}
.cell:nth-child(odd) {
  border-color: rgba(0, 0, 0, 0.3);
}
.cell.header {
  height: 35px;
  line-height: 35px;
  border-top: 2px solid rgba(0, 0, 0, 0.2);
  border-bottom: 2px solid rgba(0, 0, 0, 0.2);
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.3);
  font-family: 'PT Mono';
  font-size: 14px;
}

/* for resizer space balancer */
.cell {
  padding-right: 8px;
}
.column:first-child .cell {
  position: relative;
  padding-left: 8px;
}



